import React from 'react';
import { connect } from 'react-redux';
import { Routes, Route } from 'react-router-dom';
import { ShowUpAnimated } from 'components/animated/showUp';
import './profile.style.scss';

const MainPageView = React.lazy(() => import('./profile.view'));
const MessagesPageView = React.lazy(() => import('./messages.view'));

class Profile extends React.Component {
  constructor(props) {
    super(props);
    this.state = {};
  }
  render() {
    return (
      <Routes>
        <Route
          path="/"
          element={
            <React.Suspense fallback={<>Loading...</>}>
              <ShowUpAnimated>
                <MainPageView user={this.props.user} />
              </ShowUpAnimated>
            </React.Suspense>
          }
        />
        <Route
          path="/messages"
          element={
            <React.Suspense fallback={<>Loading...</>}>
              <ShowUpAnimated>
                <MessagesPageView
                  goto={(id) => {
                    this.props.redirect('../tasks/message/' + id);
                  }}
                  messages={this.props.messages.filter(
                    (i) => i.Status === 4 && i.Creator._id !== this.props.user.UserId
                  )}
                />
              </ShowUpAnimated>
            </React.Suspense>
          }
        />
      </Routes>
    );
  }
}

const mapStateToProps = (state) => state;
export const ProfileModule = connect(mapStateToProps)(Profile);
export default ProfileModule;
